.menu__btn.active:after, .menu__btn.active:before {
  top: 50%;
  left: 50%;
  background-color: #fff;
}

.menu__btn.active:before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.menu__btn.active:after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.menu__btn span, .menu__btn:after, .menu__btn:before {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background-color: #33558b;
  position: absolute;
  left: 0;
  transition: .2s;
}

.menu__btn:before {
  top: 0;
}

.menu__btn span {
  top: 50%;
  transform: translateY(-50%);
}

.menu__btn:after {
  bottom: 0;
}

