// Global Site Style
html, body {
	font-family: circe, sans-serif;
	line-height: 1.2;
	font-size: 16px;
	color: $black;
	background-color: #f1f1f2;
}

@media(max-width: 1024px) {
	::-webkit-scrollbar-thumb {
		background: transparent;
	}
	
	::-webkit-scrollbar {
		background-color: transparent;
	}
	
	::-webkit-scrollbar-track {
		background-color: transparent;
	}
	
	::-webkit-scrollbar-thumb:horizontal {
	  height: 0px;
	}
}


#wrapper {
	overflow: hidden;
}

.container {
	width: 1170px;
	margin: 0 auto;
	padding-left: 15px;
	padding-right: 15px;
	box-sizing: content-box;

	@media(max-width: 1220px) {
		width: 100%;
		padding-left: 22px;
		padding-right: 22px;
		box-sizing: border-box;
	}
}

.container-second {
	width: 1280px;
	margin: 0 auto;
	padding-left: 15px;
	padding-right: 15px;
	box-sizing: content-box;

	@media(max-width: 1340px) {
		width: 100%;
		padding-left: 22px;
		padding-right: 22px;
		box-sizing: border-box;
	}
}

.container-max {
	width: 1440px;
	margin: 0 auto;
	//padding-left: 15px;
	//padding-right: 15px;
	box-sizing: content-box;

	@media(max-width: 1460px) {
		width: 100%;
	}

	@media(max-width: 1439px) {
		width: 100%;
		padding-left: 22px;
		padding-right: 22px;
		box-sizing: border-box;
	}
}


//Button
.btn {
	outline: none;
	display: inline-block;
	padding: 17px 61px;
	white-space: nowrap;

	border-radius: 5px;
	box-shadow: 6px 6px 14px 0 rgba(0, 0, 0, 0.19);
	background-color: #33558b;
	color: #fff;

	font-size: 18px;
	font-weight: bold;
	font-stretch: normal;
	font-style: normal;
	letter-spacing: -1px;
	text-align: center;
	transition: .2s;
	cursor: pointer;
	box-sizing: border-box;
	border: solid 4px #33558b;

	@media(max-width: 1180px) {
		padding: 14px 55px;
		font-size: 16px;
	}

	@media(max-width: 580px) {
		font-size: 14px;
		padding: 16px 55px;
	}

	&:hover {
		box-shadow: 6px 6px 14px 0 rgba(0, 0, 0, 0.19);
		background-color: #3864a9;
		border-color: #3864a9;
	}

	&--border {
		box-shadow: 6px 6px 14px 0 rgba(0, 0, 0, 0.19);
		background-color: #f1f1f2;
		color: #1a1e25;

		&:hover {
			background-color: #3864a9;
			border-color: #3864a9;
			color: #fff;
		}
	}
}


// Headers
h1 {
	font-size: 80px;
	font-weight: normal;
	line-height: 0.9;
	letter-spacing: 0.2px;
	color: #26252c;

	@media(max-width: 1180px) {
		font-size: 65px;
		line-height: 1.1;
	}

	@media(max-width: 992px) {
		font-size: 60px;
	}

	@media(max-width: 580px) {
		font-size: 36px;
		line-height: 0.89;
		color: #26252c;
	}
}

h2 {
	font-size: 66px;
	font-weight: normal;
	line-height: 0.85;
	letter-spacing: normal;
	color: #26252c;
	position: relative;
	margin-bottom: 54px;

	@media(max-width: 992px) {
		font-size: 50px;
		line-height: 0.95;
	}

	@media(max-width: 767px) {
		font-size: 35px;
		line-height: 1;
	}

	@media(max-width: 580px) {
		font-size: 24px;
		font-weight: normal;
		line-height: 1;
	}

	&:after {
		content: "";
		display: block;
		position: absolute;
		bottom: -54px;
		left: 0;
		width: 170px;
		height: 8px;
		object-fit: contain;
		background-color: #1b1f26;

		@media(max-width: 992px) {
			width: 120px;
			height: 6px;
			bottom: -30px;
		}

		@media(max-width: 767px) {
			width: 80px;
			height: 4px;
			bottom: -20px;
		}

		@media(max-width: 580px) {
			width: 39px;
			height: 4px;
		}
	}
}

h3 {
	font-size: 46px;
	font-weight: bold;
	line-height: 1.04;
	letter-spacing: normal;
	color: #ffffff;

	@media(max-width: 992px) {
		font-size: 30px;
		line-height: 1;
	}

	@media(max-width: 767px) {
		font-size: 25px;
		line-height: 1;
	}

	@media(max-width: 580px) {
		font-size: 18px;
		font-weight: normal;
		line-height: 1;
	}
}

p {
	font-size: 18px;
	font-weight: normal;
	font-stretch: normal;
	font-style: normal;
	line-height: 1.78;
	letter-spacing: normal;
	color: #1b1f26;

	@media(max-width: 580px) {
		font-size: 16px;
		line-height: 1.5;
		color: #1b1f26;
	}
}

input {
	width: 270px;
	height: 64px;
	object-fit: contain;
	border-radius: 5px;
	box-shadow: 6px 6px 14px 0 rgba(0, 0, 0, 0.19);
	border: solid 2px #393a45;
	outline: none;
	background-color: transparent;

	padding-left: 32px;
	padding-right: 20px;
	font-size: 18px;
	font-weight: normal;
	line-height: 2.11;
	letter-spacing: normal;
	color: #26252c;

	@media(max-width: 580px) {
		padding-left: 25px;
		font-size: 16px;
	}

	&.color-white {
		color: #fff;

		&::placeholder {
			//color: #fff;
		}
	}

	&.error {
		border-color: red;
	}

	@media(max-width: 1180px) {
		width: 240px;
		height: 54px;
	}
}


.slick {
	&-dots {
		width: 100%;
		height: 8px;
		background-color: rgba(#1b1f26, 0.2);
		display: flex;

		li {
			flex: 1;
			height: 8px;
			background-color: transparent;
			cursor: pointer;
			position: relative;
			transition: .2s;

			&.slick-active {
				background-color: #33558b;
			}

			button {
				width: 100%;
				height: 100%;
				background-color: inherit;
				border: 0;
				font-size: 0;
				position: absolute;
				top: 0;
				left: 0;
				outline: 0;
				cursor: pointer;
			}
		}
	}

	&-slide {
		outline: none;
	}
}


.safari-scroll {
	-webkit-overflow-scrolling: touch;
}


//Blocks Order
#wrapper {
	@media(max-width: 768px) {
		display: flex;
		flex-direction: column;

		.menu {
			order: 1;
		}

		.header {
			order: 2;
		}

		.bstylist {
			order: 3;
		}

		.brands {
			order: 4;
		}

		.bourall {
			order: 5;
		}

		.stylistwork {
			order: 10;
		}

		.reghaircut {
			order: 11;
		}

		.teamstyl {
			order: 8;
		}

		.stylstudy {
			order: 9;
		}

		.registration {
			order: 5;
		}

		.reputation {
			order: 11;
		}

		.footer {
			order: 12;
		}
	}
}

// @media (max-width: 992px) { .rellax { transform: translate3d(0,0,0) !important; } }
.animitem {

	@media(max-width: 992px) {
		display: none !important;
	}

	&:after {
		background-color: #2e343e;
		opacity: 0.1;
		width: 100%;
		height: 100%;
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
	}

	&.half-hor {
		width: 170px;
		height: 340px;
		//background: url("ico/anim-item.svg") center center no-repeat;
		position: absolute;
		right: 0;
		&:after {
			border-radius: 340px 0 0 340px;
		}
	}

	&.half-hor2 {
		width: 170px;
		height: 340px;
		//background: url("ico/anim-item.svg") center center no-repeat;
		position: absolute;
		right: 0;

		&:after {
			border-radius: 0 340px 340px 0;
		}
	}

	&.half-ver {
		height: 170px;
		width: 340px;
		position: absolute;
		left: 0;

		&:after {
			border-radius: 340px 340px 0 0;
		}
	}

	&.half-ver2 {
		height: 170px;
		width: 340px;
		position: absolute;
		left: 0;

		&:after {
			border-radius: 0 0 340px 340px;
		}
	}
}


.fancybox-button {

	svg {
		transition: .7s;
		transform-origin: center center;

		&:hover {
			transform: rotate(360deg);
		}
	}
}

.block-scroll {
	overflow: hidden;
}

.fancybox-container .fancybox-bg {
	// opacity: 0.82;
	background-color: #ffffff;
}

.fancybox-is-open .fancybox-bg {
	opacity: 0.82;
}


